"use client";

import { useRef } from "react";
import { useLayoutEffect } from "react";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

export default function VisibloSpotlight() {
    const sectionRef = useRef(null);

    useLayoutEffect(() => {
        const ctx = gsap.context(() => {
            // Dashboard float animation
            gsap.to(".dashboard-mockup", {
                y: -15,
                duration: 3,
                repeat: -1,
                yoyo: true,
                ease: "sine.inOut",
            });

            // Content reveal
            gsap.from(".spotlight-content > *", {
                scrollTrigger: {
                    trigger: sectionRef.current,
                    start: "top 70%",
                },
                y: 50,
                opacity: 0,
                stagger: 0.1,
                duration: 0.8,
                ease: "power3.out",
            });

            // Stats count animation
            gsap.from(".stat-number", {
                scrollTrigger: {
                    trigger: ".stats-row",
                    start: "top 80%",
                },
                textContent: 0,
                duration: 2,
                ease: "power2.out",
                snap: { textContent: 1 },
            });

        }, sectionRef);

        return () => ctx.revert();
    }, []);

    return (
        <section ref={sectionRef} className="py-32 bg-bg-accent relative overflow-hidden">

            {/* Background decoration */}
            <div className="absolute top-0 right-0 w-1/2 h-full bg-gradient-to-l from-neon-blue/5 to-transparent pointer-events-none" />

            <div className="container-candy">

                {/* Badge */}
                <div className="inline-block px-4 py-2 bg-gradient-to-r from-neon-blue to-future-purple text-white rounded-full text-sm font-bold mb-8">
                    ðŸš€ LAUNCHING Q2 2025
                </div>

                <div className="grid lg:grid-cols-2 gap-16 items-center">

                    {/* Left: Dashboard Mockup */}
                    <div className="dashboard-mockup relative">
                        <div className="gradient-border-card p-6 bg-white shadow-2xl">

                            {/* Dashboard Header */}
                            <div className="flex items-center justify-between mb-6 pb-4 border-b border-gray-100">
                                <h4 className="font-display text-xl text-text-dark">Visibility Score</h4>
                                <div className="flex items-center gap-2">
                                    <div className="pulse-dot" />
                                    <span className="text-sm text-text-light">Live</span>
                                </div>
                            </div>

                            {/* Big Number */}
                            <div className="mb-6">
                                <div className="text-7xl font-display gradient-text">84.2</div>
                                <div className="flex items-center gap-2 mt-2">
                                    <span className="text-green-500 font-bold">â–² 12.3%</span>
                                    <span className="text-text-light text-sm">vs last week</span>
                                </div>
                            </div>

                            {/* Platform Bars */}
                            <div className="space-y-4">
                                {[
                                    { name: "ChatGPT", value: 92, color: "bg-neon-blue" },
                                    { name: "Claude", value: 78, color: "bg-future-purple" },
                                    { name: "Perplexity", value: 85, color: "bg-cyber-pink" },
                                    { name: "Gemini", value: 71, color: "bg-energy-orange" },
                                ].map((platform, i) => (
                                    <div key={i}>
                                        <div className="flex justify-between text-sm mb-1">
                                            <span className="text-text-gray">{platform.name}</span>
                                            <span className="font-bold text-text-dark">{platform.value}%</span>
                                        </div>
                                        <div className="h-2 bg-gray-100 rounded-full overflow-hidden">
                                            <div
                                                className={`h-full ${platform.color} rounded-full`}
                                                style={{ width: `${platform.value}%` }}
                                            />
                                        </div>
                                    </div>
                                ))}
                            </div>

                        </div>

                        {/* Floating badges around dashboard */}
                        <div className="absolute -top-4 -right-4 px-4 py-2 bg-white rounded-full shadow-lg float border-2 border-neon-blue">
                            <span className="text-sm font-bold text-neon-blue">ðŸ“Š Real-time</span>
                        </div>
                        <div className="absolute -bottom-4 -left-4 px-4 py-2 bg-white rounded-full shadow-lg float float-delay-1 border-2 border-cyber-pink">
                            <span className="text-sm font-bold text-cyber-pink">ðŸŽ¯ AI-powered</span>
                        </div>
                    </div>

                    {/* Right: Content */}
                    <div className="spotlight-content">
                        <h2 className="font-display text-5xl md:text-6xl text-text-dark mb-6">
                            <span className="gradient-text">VISIBLO</span>
                        </h2>

                        <p className="text-xl text-text-gray mb-8 leading-relaxed">
                            Master your AI visibility. Track your brand across 150+ AI platforms,
                            get visibility scores, competitor intel, and actionable insights.
                        </p>

                        {/* Stats */}
                        <div className="stats-row grid grid-cols-3 gap-6 mb-10">
                            {[
                                { number: "10M+", label: "Queries" },
                                { number: "150+", label: "Platforms" },
                                { number: "24/7", label: "Monitoring" },
                            ].map((stat, i) => (
                                <div key={i} className="text-center">
                                    <div className="stat-number text-3xl font-display gradient-text">{stat.number}</div>
                                    <div className="text-sm text-text-light">{stat.label}</div>
                                </div>
                            ))}
                        </div>

                        {/* CTA */}
                        <button className="btn-glow px-10 py-5 bg-gradient-to-r from-neon-blue to-cyber-pink text-white rounded-full font-bold text-lg">
                            Join Waitlist â†’
                        </button>
                    </div>

                </div>
            </div>
        </section>
    );
}
